.about-hero {
  background-color: var(--primary-color);
  color: var(--text-light);
  padding: 100px 0 50px;
  text-align: center;
}

.about-hero h1 {
  font-size: 3rem;
  margin-bottom: 10px;
}

.about-hero p {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto;
}

.about-content {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 40px;
  align-items: start;
}

.about-grid {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 50px;
  align-items: start;
}

.about-text h2 {
  color: var(--primary-color);
  margin-bottom: 20px;
  margin-top: 30px;
}

.about-text h2:first-child {
  margin-top: 0;
}

.about-text p {
  margin-bottom: 20px;
  line-height: 1.8;
}

.values-list {
  list-style: none;
  padding: 0;
}

.values-list li {
  margin-bottom: 25px;
}

.value-title {
  font-weight: 600;
  color: var(--primary-color);
  font-size: 1.1rem;
  display: block;
  margin-bottom: 8px;
}

.about-image {
  position: relative;
}

.about-image img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.office-image {
  width: 100%;
  border-radius: 8px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.about-quote {
  background-color: var(--secondary-color);
  padding: 30px;
  width: 80%;
  position: absolute;
  bottom: -30px;
  right: -30px;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.about-quote p {
  color: var(--primary-color);
  font-weight: 600;
  font-style: italic;
  text-align: center;
  margin: 0;
  font-size: 1.1rem;
}

.process-section {
  background-color: #f9f9f9;
}

.process-steps {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin-top: 40px;
}

.process-step {
  text-align: center;
  padding: 30px 20px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  position: relative;
}

.step-number {
  width: 50px;
  height: 50px;
  background-color: var(--secondary-color);
  color: var(--primary-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: 700;
  margin: 0 auto 20px;
}

.process-step h3 {
  margin-bottom: 15px;
  color: var(--primary-color);
}

@media screen and (max-width: 992px) {
  .about-content {
    grid-template-columns: 1fr;
  }
  
  .about-image {
    margin-top: 30px;
  }
  
  .about-quote {
    width: 70%;
    bottom: -20px;
    right: -20px;
  }
  
  .process-steps {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 768px) {
  .about-grid {
    grid-template-columns: 1fr;
  }
  
  .about-image {
    order: -1; /* Places image above text on mobile */
  }
}

@media screen and (max-width: 576px) {
  .about-hero h1 {
    font-size: 2.5rem;
  }
  
  .about-quote {
    position: relative;
    width: 100%;
    bottom: 0;
    right: 0;
    margin-top: 20px;
  }
  
  .process-steps {
    grid-template-columns: 1fr;
  }
}
