.featured-section {
  background-color: #f9f9f9;
}

.vehicle-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
}

.vehicle-card {
  background-color: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.vehicle-card:hover {
  transform: translateY(-10px);
}

.vehicle-image-container {
  width: 100%;
  height: auto;
  overflow: hidden;
  aspect-ratio: 1.5/1;
}

.vehicle-image {
  width: 100%;
  height: auto;
  object-fit: contain;
  transition: transform 0.5s ease;
}

.vehicle-card:hover .vehicle-image {
  transform: scale(1.05);
}

.vehicle-name {
  padding: 15px;
  text-align: center;
  font-weight: 600;
}

.why-choose-section {
  background-color: white;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 30px;
}

.feature-card {
  padding: 30px;
  text-align: center;
  border-radius: 8px;
  background-color: #f9f9f9;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
}

.feature-icon {
  font-size: 40px;
  margin-bottom: 15px;
}

.feature-card h3 {
  margin-bottom: 15px;
  color: var(--primary-color);
}

@media screen and (max-width: 768px) {
  .vehicle-grid,
  .features-grid {
    grid-template-columns: 1fr;
  }
}
