.promotion-banner {
  background-color: var(--primary-color);
  color: var(--text-light);
  padding: 40px 0;
  text-align: center;
}

.promotion-content {
  max-width: 800px;
  margin: 0 auto;
}

.promotion-content h3 {
  color: var(--secondary-color);
  font-size: 28px;
  margin-bottom: 15px;
}

.promotion-content p {
  margin-bottom: 20px;
  font-size: 18px;
  line-height: 1.6;
}

.promotion-btn {
  font-size: 16px;
  padding: 12px 30px;
  font-weight: 600;
}

@media screen and (max-width: 768px) {
  .promotion-banner {
    padding: 30px 0;
  }
  
  .promotion-content h3 {
    font-size: 24px;
  }
  
  .promotion-content p {
    font-size: 16px;
  }
}
