.contact-hero {
  background-color: var(--primary-color);
  color: var(--text-light);
  padding: 100px 0 50px;
  text-align: center;
}

.contact-hero h1 {
  font-size: 3rem;
  margin-bottom: 10px;
}

.contact-hero p {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto;
}

.contact-grid {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 50px;
}

.contact-info h2,
.contact-form-container h2 {
  margin-bottom: 30px;
  color: var(--primary-color);
}

.info-item {
  margin-bottom: 25px;
}

.info-item h3 {
  color: var(--primary-color);
  margin-bottom: 8px;
  font-size: 18px;
}

.promotion-info {
  margin-top: 40px;
  padding: 20px;
  background-color: #f9f9f9;
  border-left: 4px solid var(--secondary-color);
  border-radius: 4px;
}

.promotion-info h3 {
  color: var(--primary-color);
  margin-bottom: 10px;
}

.contact-form-container {
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  border-color: var(--secondary-color);
  outline: none;
}

.submit-btn {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  margin-top: 10px;
}

.success-message {
  padding: 20px;
  background-color: #e8f5e9;
  border-left: 4px solid #4caf50;
  border-radius: 4px;
  margin-bottom: 20px;
}

.success-message p {
  color: #2e7d32;
  margin: 0;
}

@media screen and (max-width: 992px) {
  .contact-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .contact-info {
    padding-bottom: 20px;
  }
}

@media screen and (max-width: 576px) {
  .contact-hero h1 {
    font-size: 2.5rem;
  }
  
  .contact-form-container {
    padding: 20px;
  }
}
