.hero-section {
  height: 600px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url('../assets/logo-bg.jpg');
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: var(--text-light);
  padding: 0 20px;
  overflow: hidden;
}

.hero-content {
  text-align: center;
  max-width: 800px;
  z-index: 1;
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  font-weight: 700;
  line-height: 1.2;
}

.highlight {
  color: var(--secondary-color);
}

.hero-content p {
  font-size: 1.2rem;
  margin-bottom: 40px;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.btn-outline {
  background-color: transparent;
  border: 2px solid var(--secondary-color);
  color: var(--text-light);
}

.btn-outline:hover {
  background-color: var(--secondary-color);
  color: var(--primary-color);
}

.deposit-badge {
  position: absolute;
  bottom: 40px;
  right: 40px;
  background-color: var(--secondary-color);
  width: 120px;
  height: 120px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: rotate(-10deg);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.badge-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  text-align: center;
}

.badge-text {
  font-size: 14px;
  font-weight: 700;
  line-height: 1;
}

.badge-percentage {
  font-size: 36px;
  font-weight: 800;
  line-height: 1;
  margin: 2px 0;
}

@media screen and (max-width: 768px) {
  .hero-section {
    height: 500px;
  }
  
  .hero-content h1 {
    font-size: 2rem;
  }
  
  .hero-content p {
    font-size: 1rem;
  }
  
  .hero-buttons {
    flex-direction: column;
    gap: 15px;
  }
  
  .deposit-badge {
    width: 100px;
    height: 100px;
    bottom: 20px;
    right: 20px;
  }
  
  .badge-percentage {
    font-size: 28px;
  }
}
