:root {
  --primary-color: #000000;
  --secondary-color: #F1C40F;
  --accent-color: #FFCB05;
  --text-light: #ffffff;
  --text-dark: #333333;
  --background-light: #f5f5f5;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  line-height: 1.6;
  color: var(--text-dark);
  background-color: var(--background-light);
}

.container {
  width: 100%;
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}

.btn {
  display: inline-block;
  background-color: var(--secondary-color);
  color: var(--primary-color);
  padding: 10px 25px;
  border: none;
  border-radius: 5px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn:hover {
  background-color: #f0b400;
  transform: translateY(-2px);
}

.section {
  padding: 60px 0;
}

.section-title {
  font-size: 2.5rem;
  margin-bottom: 30px;
  text-align: center;
  color: var(--primary-color);
}

.section-subtitle {
  font-size: 1.2rem;
  margin-bottom: 40px;
  text-align: center;
}

@media (max-width: 768px) {
  .section {
    padding: 40px 0;
  }
  
  .section-title {
    font-size: 2rem;
  }
}
